import React, { useState } from "react";
import Shop from "./compenents/Shop/Shop";
import "./App.css";
import MainHeader from "./compenents/MainHeader/MainHeader";
import MainFooter from "./compenents/MainFooter/MainFooter";
import { sneakers } from "./data/products";

function App() {
  const [products, setProducts] = useState(sneakers);

  const handleCartChange = (index, delta) => {
    setProducts([...products], {
      cart: (products[index].cart += delta),
    });
  };

  return (
    <div className="App">
      <MainHeader />
      <main className="App-main">
        <Shop sneakers={products} handleCartChange={handleCartChange} />
        {/* <section className="Cart">
          <div></div>
          <div></div>
        </section> */}
      </main>
      <MainFooter sneakers={products} />
    </div>
  );
}

export default App;
